<template>
  <div class="message-container">
    <template v-if="userId">
      <dl class="users-list">
        <dt class="users-title">
          <span class="title">消息</span>
          <span class="num">{{unreadCount}}条未读</span>
        </dt>
        <van-list
          v-if="!list || list.length > 0"
          v-model="isLoading"
          :finished="finished"
          finished-text="没有更多了"
          @load="fetchMessage"
          class="person-list"
        >
          <dd class="users-item" v-for="(item, index) in list" :key="index" @click="jump(item.id)">
            <div class="head-box">
              <div class="img-box">
                <img :src="`/api/member/pic?id=${item.coverPicList?item.coverPicList[0]:0}&type=1&targetMemberId=${item.id}`" alt="" class="img" />
              </div>
              <div v-show="item.status == 0">
                <span class="sentence-num"></span>
              </div>
            </div>
            <div class="infomation" @click="jump(item.fromMemberId, item.id, index)">
              <p class="name-box"><span class="name">{{item.name}}</span></p>
              <p class="sentence">{{item.message}}</p>
            </div>
          </dd>
        </van-list>
        <dd v-else>
          <p class="no-message-tips">当前暂未收到消息</p>
          <img src="~@/assets/img/no-search-result.png" alt="" class="no-message-img" />
        </dd>
      </dl>

      <van-dialog v-model="isShowDialog" :confirmButtonText="dialogConfirmText" @confirm="confirm" @closed="closed">
        <div class="user-message-dialog">
          <p class="tips">对于喜欢你的人，你只须要回点ta一个喜欢，即可免费获得对方的微信号</p>
          <div class="not-show-again" @click="checkKnown">
            <span class="check-box" :class="{'isChecked': notShowAgain}"></span>
            <span class="known">已知悉，不在显示该提示</span>
          </div>
        </div>
      </van-dialog>
    </template>

    <Login v-else />
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from 'vuex'
import Storage from '@/utils/Storage'
import Login from '@/components/common/login'
export default {
  name: 'account',
  components: {
    Login
  },
  data () {
    return {
      list: undefined,
      currentId: undefined,
      isShowDialog: false,
      dialogConfirmText: '我知道了',
      notShowAgain: false,
      unWatcher: null,
      currentPage: 1,
      pageSize: 20,
      isLoading: false,
      finished: false
    }
  },
  computed: {
    ...mapState({
      userData: 'userData'
    }),
    ...mapState('message', {
      unreadCount: 'unreadCount'
    }),
    ...mapGetters({
      userId: 'userId'
    })
  },
  methods: {
    ...mapMutations('message', {
      updateUnreadCount: 'updateUnreadCount'
    }),
    jump (userId, id, index) {
      this.$root.openLoading()
      this.$http('/api/member/message/read', {
        currentMemberId: this.userId,
        messageId: id
      }, 'get').then(res => {
        this.$root.closeLoading()
        if (res.code === '0000') {
          this.list[index].status = 1
          if (this.unreadCount > 0) {
            this.updateUnreadCount(this.unreadCount - 1)
          }
          this.$router.push({
            path: `/message-detail-${userId}.html`
          })
        } else {
          this.$toast(res.msg)
        }
        this.isLoading = false
      }, err => {
        this.isLoading = false
        this.$root.closeLoading()
        this.$toast(err)
      })
    },
    toDetail () {
      this.$router.push({
        path: `/info-${this.currentId}.html`
      })
    },
    popup (id) {
      this.currentId = id
      if (!Storage.load('neverPopMessage')) {
        this.isShowDialog = true
        return
      }

      // 直接跳转到个人详情页
      this.toDetail()
    },
    checkKnown () {
      this.notShowAgain = !this.notShowAgain
    },
    confirm () {
      if (this.notShowAgain) {
        Storage.save('neverPopMessage', true)
      } else {
        Storage.save('neverPopMessage', false)
      }
    },
    closed () {
      // 直接跳转到个人详情页
      this.toDetail()
    },
    fetchMessage () {
      this.isLoading = true
      this.$http('/api/member/message/list', {
        currentMemberId: this.userId,
        currentPage: this.currentPage,
        pageSize: this.pageSize
      }, 'get').then(res => {
        this.isLoading = false
        if (res.code === '0000') {
          this.list = res.data.data || []
          if (this.currentPage >= res.data.pageCount) {
            this.finished = true
          } else {
            this.finished = false
          }
          this.currentPage++
        } else {
          res.code !== '100005' && this.$toast(res.msg)
          this.finished = true
        }
      }, err => {
        this.isLoading = false
        this.$toast(err)
      })
    }
  },
  activated () {
    this.unWatcher = this.$watch('unreadCount', (val) => {
      if (parseInt(val) >= 0) {
        this.currentPage = 1
        this.fetchMessage()
      }
    })
  },
  deactivated () {
    this.unWatcher && this.unWatcher()
  },
  mounted () {
    this.fetchMessage()
  }
}
</script>

<style lang="scss">
  @import "@/assets/scss/constant.scss";
  .message-container {
    .no-message-tips {
      margin-top: 100px;
      color: #9B9B9B;
      font-size: 26px;
      line-height: 38px;
      text-align: center;
    }
    .no-message-img {
      margin: 60px auto 0;
      display: block;
      width: 166px;
    }
    .user-message-dialog {
      padding: 40px 28px 40px 36px;
      .tips {
        color: #9B9BA1;
        font-size: 28px;
        line-height: 40px;
      }
      .not-show-again {
        margin-top: 40px;
      }
      .check-box {
        display: inline-block;
        vertical-align: middle;
        width: 40px;
        height: 40px;
        border-radius: 2px;
        border: 1px solid #979797;
      }
      .known {
        display: inline-block;
        vertical-align: middle;
        margin-left: 10px;
        color: #9B9BA1;
        font-size: 28px;
        line-height: 40px;
      }
    }
  }
</style>
