<template>
  <div class="like-detail-container">
    <p class="title-con">
      <span class="title">“喜欢”使用明细</span>
      <span class="num">共{{total}}条记录</span>
    </p>
    <van-list
      v-model="isLoading"
      :finished="isFinished"
      finished-text="没有更多了"
      @load="fetchData"
      class="list"
    >
      <li class="item" v-for="(item, index) in list" :key="index">
        <p class="name">{{item.type === 1 ? '购买' : '消耗'}}1个</p>
        <p class="sub">
          <span class="left">剩余{{item.remain}}个</span>
          <span class="date">{{item.createTime | formatTime}}</span>
        </p>
      </li>
    </van-list>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'LikeDetail',
  data () {
    return {
      isLoading: false,
      isFinished: false,
      currentPage: 1,
      pageSize: 20,
      list: [],
      total: 0
    }
  },
  computed: {
    ...mapGetters({
      userId: 'userId'
    })
  },
  filters: {
    formatTime (timestamp) {
      try {
        const date = new Date(timestamp)
        let month = date.getMonth() + 1
        month = month < 10 ? '0' + month : month
        let day = date.getDate()
        day = day < 10 ? '0' + day : day
        let hour = date.getHours()
        hour = hour < 10 ? '0' + hour : hour
        let minutes = date.getMinutes()
        minutes = minutes < 10 ? '0' + minutes : minutes
        let seconds = date.getSeconds()
        seconds = seconds < 10 ? '0' + seconds : seconds
        return `${date.getFullYear()}-${month}-${day} ${hour}:${minutes}:${seconds}`
      } catch (err) {
        return ''
      }
    }
  },
  methods: {
    fetchData () {
      this.isLoading = true
      this.$http('/api/member/action/bill', {
        currentPage: this.currentPage,
        pageSize: this.pageSize,
        currentMemberId: this.userId,
        actionType: 20
      }, 'get').then(res => {
        this.isLoading = false
        if (res.code === '0000' && res.data) {
          const data = res.data
          if (this.currentPage === 1) {
            this.list = data.records
          } else {
            this.list = [...this.list, ...data.records]
          }
          this.total = data.total
          if (this.currentPage >= data.pages || !this.userId) {
            this.isFinished = true
          }
          this.currentPage++
        } else {
          this.isFinished = true
          this.$toast(res.msg)
        }
      }, err => {
        this.isFinished = true
        this.isLoading = false
        this.$toast(err)
      })
    }
  }
}
</script>

