    font-weight: bold;
  }
  .num {
    color: #9b9b9b;
  }
  .list {
    padding-left: 40px;
  }
  .item {
    padding: 32px 40px 5px 0;
    .name {
      color: #38383C;
      font-size: 32px;
      line-height: 48px;
    }
    .sub {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .date, .left {
      color: #9B9B9B;
