/* eslint-disable no-unused-vars */
<template>
  <div class="home-outer-container">
    <MyHeader ref="header">
      <div class="btn-con" slot="nav-left"></div>
      <div class="btn-con" slot="nav-right">
        <a href="javascript:;" @click.stop="switchSearchInput" class="header-btn"><img src="~@/assets/img/search.png"
            class="search-icon" /></a>
      </div>
    </MyHeader>
    <div class="top-function">
      <transition name="scaleHeight">
        <div class="search-box" v-if="isShowSearch">
          <div class="search-con">
            <input type="text" v-model="searchId" class="input" placeholder="请输入用户ID查询" />
            <a href="javascript:;" class="search-btn" @click.stop="fetchInfoById">搜索</a>
          </div>
        </div>
      </transition>

      <!-- 搜索结果弹窗 -->
      <van-popup v-model="showSearchResult" position="top">
        <div class="searching" v-if="searching">
          <loadingIcon />
        </div>
        <div class="person-item img-box" @click.stop="toInfo(searchResult.id)" v-else-if="searchResult.id">
          <img class="img"
            v-lazy="`/api/member/pic?id=${searchResult.coverPicList && searchResult.coverPicList[0]}&type=1&targetMemberId=${searchResult.id}`" />
          <span class="img-num">{{searchResult.coverPicList.length}}</span>
          <div class="info-box">
            <div class="id-con">
              <span class="name-con">{{searchResult.nickName}}<img
                  v-if="parseInt(searchResult.identityAuth) === 3 && parseInt(searchResult.academicAuth) === 3 && parseInt(searchResult.workAuth) === 3"
                  class="identify-icon" src="~@/assets/img/identify.png" /></span>
              <span class="id">ID: {{searchResult.id}}</span>
            </div>
            <p class="info-con desc">{{searchResult.age ? `${searchResult.age}岁 ` : ''}}
              {{searchResult.high ? `${searchResult.high}cm ` : ''}}{{educationMap[searchResult.education]}}
              {{searchResult.job}}{{searchResult.company ?`（${searchResult.company}）`: ''}}</p>
            <p class="info-con location">
              {{searchResult.cityId ? `现居${parseInt(searchResult.cityId) === 900000 ? (searchResult.country + ' ' + searchResult.state) : areaData['city_list'][searchResult.cityId]}` : ''}}
              {{searchResult.cityId && searchResult.nativeCityId ? ' · ' : ''}}{{searchResult.nativeCityId ? `${parseInt(searchResult.nativeCityId) === 900000 ? (searchResult.nativeCountry + ' ' + searchResult.nativeState) : areaData['city_list'][searchResult.nativeCityId]}人` : ''}}
            </p>
          </div>
        </div>
        <div class="no-searchResult" v-else>
          <img src="~@/assets/img/no-search-result.png" alt="" class="no-result-img" />
          <p class="no-result-tips">未搜索到对应用户</p>
        </div>
      </van-popup>

      <div class="combo-con">
        <router-link to="/combo.html" class="combo-link common-bg">
          <span class="con-left"><img class="stamp" src="~@/assets/img/bitmap.png" />加入实名单身群，1对1服务脱单更高效</span>
          <img class="arrow-right" src="~@/assets/img/arrow-right.png" />
        </router-link>
      </div>
    </div>

    <div class="home-container">
      <van-list v-model="isLoading" :finished="finished" :finished-text="completeInfo ? '' : '没有更多了'" @load="onLoad"
        class="person-list">
        <li class="person-item img-box" v-for="item in list" :key="item.id" @click.stop="toInfo(item.id)">
          <img class="img"
            v-lazy="`/api/member/pic?id=${item.coverPicList && item.coverPicList[0]}&type=1&targetMemberId=${item.id}`" />
          <span class="img-num">{{item.coverPicList.length}}</span>
          <div class="info-box">
            <div class="id-con">
              <span class="name-con">{{item.nickName}}<img
                  v-if="parseInt(item.identityAuth) === 3 && parseInt(item.academicAuth) === 3 && parseInt(item.workAuth) === 3"
                  class="identify-icon" src="~@/assets/img/identify.png" /></span>
              <span class="id">ID: {{item.id}}</span>
            </div>
            <p class="info-con desc">{{item.age ? `${item.age}岁 ` : ''}}
              {{item.high ? `${item.high}cm ` : ''}}{{educationMap[item.education]}}
              {{item.job}}{{item.company ?`（${item.company}）`: ''}}</p>
            <p class="info-con location">
              {{item.cityId ? `现居${parseInt(item.cityId) === 900000 ? (item.country + ' ' + item.state) : areaData['city_list'][item.cityId]}` : ''}}
              {{item.cityId && item.nativeCityId ? ' · ' : ''}}{{item.nativeCityId ? `${parseInt(item.nativeCityId) === 900000 ? (item.nativeCountry + ' ' + item.nativeState) : areaData['city_list'][item.nativeCityId]}人` : ''}}
            </p>
          </div>
        </li>
      </van-list>
    </div>

    <div class="complete-info-pop" v-if="completeInfo">
      <p class="tips">真诚、严肃、平等交友<br />查看更多优质会员，请先完善个人资料</p>
      <a href="javascript:;" class="jump-btn" @click="jump">{{buttonText}}</a>
    </div>

    <!-- 加速审核弹窗 -->
    <AccelerateAduit v-model="isShowAccelerate" @confirm="isShowAccelerate = false" @close="isShowAccelerate = false" />
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import MyHeader from '@/components/common/myHeader'
import loadingIcon from '@/components/common/loadingIcon'
import AccelerateAduit from '@/components/dialog/accelerateAduit'

export default {
  name: 'Home',
  components: {
    MyHeader,
    loadingIcon,
    AccelerateAduit
  },
  data () {
    return {
      isShowSearch: false,
      searchId: '',
      searching: false,
      showSearchResult: false,
      hadResult: true,
      searchResult: {},
      isLoading: false,
      finished: false,
      list: [],
      currentPage: 1,
      pageSize: 20,
      // 完善资料弹窗
      completeInfo: false,
      isShowAccelerate: false
    }
  },
  computed: {
    ...mapState({
      userData: 'userData',
      educationMap: 'educationMap',
      areaData: 'areaData'
    }),
    ...mapGetters({
      userId: 'userId'
    }),
    buttonText () {
      if (!this.userId) return '请登录'
      let { basedataAuth } = this.userData
      if (basedataAuth === 0) return '请先完善个人资料'
      if (basedataAuth === 1) return '个人资料审核中'
      if (basedataAuth === 2) return '个人资料未通过审核，请修改重新提交'

      let map = { identityAuth: '身份', academicAuth: '学历', workAuth: '工作' }

      for (let index = 0, keys = Object.keys(map); index < keys.length; index++) {
        let item = keys[index]; let label = map[item]; let option = this.userData[item]
        if (option === 0) return `请进行${label}认证`
        if (option === 1) return `${label}认证等待审核中`
        if (option === 2) return `${label}认证未通过审核，请修改重新提交`
      }
      return ''

      /*   if (!this.userId) {
        msg = '请登录'
      } else if (parseInt(this.userData.basedataAuth) === 0) {
        msg = '请完善个人资料'
      } else if (parseInt(this.userData.basedataAuth) === 1) {
        msg = '等待资料审核中'
      } else if (parseInt(this.userData.basedataAuth) === 2) {
        msg = '资料未通过审核，请修改重新提交'
      } else if (parseInt(this.userData.identityAuth) === 0 || parseInt(this.userData.academicAuth) === 0 || parseInt(this.userData.workAuth) === 0) {
        msg = '请进行实名认证'
      } else if (parseInt(this.userData.identityAuth) === 1 || parseInt(this.userData.academicAuth) === 1 || parseInt(this.userData.workAuth) === 1) {
        msg = '等待实名审核中'
      } else if (parseInt(this.userData.identityAuth) === 2 || parseInt(this.userData.academicAuth) === 2 || parseInt(this.userData.workAuth) === 2) {
        msg = '证件未通过审核，请修改重新提交'
      } */
    },
    isAuthUser () {
      return parseInt(this.userData.basedataAuth) === 3 &&
        parseInt(this.userData.identityAuth) === 3 &&
        parseInt(this.userData.academicAuth) === 3 &&
        parseInt(this.userData.workAuth) === 3
    }
  },
  watch: {
    showSearchResult (val) {
      if (val) {
        this.$refs['header'].$el.style.zIndex = 3000
      } else {
        setTimeout(() => {
          this.$refs['header'].$el.style.zIndex = 99
        }, 500)
      }
    },
    userData (val, oldVal) {
      if (!val || !oldVal) return
      if (this.isAuthUser) {
        this.completeInfo = false
        window.removeEventListener('scroll', this.scroll)
      }
      // 择偶条件更改时，重新加载列表数据
      if (
        val.selectCountry !== oldVal.selectCountry ||
        val.selectState !== oldVal.selectState ||
        val.selectionAcadmic !== oldVal.selectionAcadmic ||
        val.selectionAgeMax !== oldVal.selectionAgeMax ||
        val.selectionAgeMin !== oldVal.selectionAgeMin ||
        val.selectionCityId !== oldVal.selectionCityId ||
        val.selectionCountryId !== oldVal.selectionCountryId ||
        val.selectionHighMax !== oldVal.selectionHighMax ||
        val.selectionHighMin !== oldVal.selectionHighMin ||
        val.selectionSingleStatus !== oldVal.selectionSingleStatus ||
        val.selectCountry !== oldVal.selectCountry ||
        val.selectState !== oldVal.selectState
      ) {
        this.currentPage = 1
        this.list = []
        this.completeInfo = false
        this.isLoading = true
        this.onLoad()
      }
    }
  },
  methods: {
    switchSearchInput () {
      this.isShowSearch = !this.isShowSearch
      if (this.isShowSearch) {
        this.$nextTick(() => {
          document.querySelector('.input').focus()
        })
      }
    },
    onLoad () {
      this.$http('/api/member/list', {
        currentPage: this.currentPage,
        pageSize: this.pageSize
      }, 'get').then(res => {
        this.isLoading = false
        if (res.code === '0000') {
          const data = res.data
          if (this.currentPage === 1) {
            this.list = data.data
          } else {
            this.list = [...this.list, ...data.data]
          }
          if (this.currentPage === data.pageCount || !this.userId || !this.isAuthUser) {
            this.finished = true
          } else {
            this.currentPage++
          }
        } else {
          this.finished = true
          res.code !== '100005' && this.$toast(res.msg)
        }
      }, () => {
        this.finished = true
        this.isLoading = false
      })
    },
    fetchInfoById () {
      if (!this.userId) {
        this.$toast('使用本功能需要先登录')
        this.$router.push({
          path: '/account.html'
        })
        return
      }
      if (!/^\d{2,6}$/.test(this.searchId)) {
        this.$toast('请输入正确的用户id')
        return
      }
      this.searching = true
      this.showSearchResult = true
      this.$http('/api/member/info', {
        targetMemberId: this.searchId
      }, 'get').then(res => {
        this.searching = false
        if (res.code === '0000') {
          this.searchResult = res.data
        } else {
          this.searchResult = {}
          this.$toast(res.msg)
        }
      }, () => {
        this.searching = false
      })
    },
    toInfo (id) {
      this.$router.push({
        path: `/info-${id}.html`
      })
    },
    jump () {
      const baseAuth = parseInt(this.userData.basedataAuth)
      const identityAuth = parseInt(this.userData.identityAuth)
      const academicAuth = parseInt(this.userData.academicAuth)
      const workAuth = parseInt(this.userData.workAuth)
      // 先判断完基础资料审核状态
      if (!this.userId) {
        this.$router.push({
          path: '/account.html'
        })
      } else if (parseInt(this.userData.status) === 0) {
        this.$router.push({
          path: '/setUserData/sex.html'
        })
      } else if (baseAuth === 0 || baseAuth === 2) {
        this.$router.push({
          path: '/baseInfo.html'
        })
      } else if (
        identityAuth === 0 ||
        academicAuth === 0 ||
        workAuth === 0 ||
        identityAuth === 2 ||
        academicAuth === 2 ||
        workAuth === 2
      ) {
        this.$router.push({
          path: '/account.html'
        })
      } else if (
        baseAuth === 1 ||
        identityAuth === 1 ||
        academicAuth === 1 ||
        workAuth === 1
      ) {
        this.isShowAccelerate = true
      }
      this.completeInfo = false
    },
    scroll () {
      if (window.innerHeight + window.pageYOffset + 100 >= document.body.offsetHeight) {
        this.completeInfo = true
      }
    }
  },
  activated () {
    if (!this.userId || !this.isAuthUser) {
      // 暂时屏蔽拦截功能
      window.addEventListener('scroll', this.scroll)
    }
  },
  beforeRouteLeave (to, from, next) {
    this.completeInfo = false
    window.removeEventListener('scroll', this.scroll)
    next()
  }
}
</script>
