<template>
  <div class="upload-cert-container upload-education-container">
    <template v-if="status === 0">
      <div class="upload-tips">拍照上传学历证书</div>
      <div class="photo-item">
        <img v-if="educationPicList && educationPicList.length" :src="`/api/member/pic?id=${educationPicList[0]}&type=5&targetMember=${userId}`" alt="" class="img" />
        <img v-else src="~@/assets/img/cert-education.png" alt="点击拍摄毕业证书" class="img"/>
        <input type="file" class="upload-btn" accept="image/*" @change="handleImg"/>
      </div>
      <a href="javascript:;" class="commit common-btn" :class="{disable: !educationPicList || educationPicList.length === 0}" @click="commit">提交认证</a>
    </template>
    <template v-else>
      <div class="process">
        <ul class="process-list">
          <li class="process-item">
            <p class="date">{{userData.academicCommitTime | formatTime}}</p>
            <p class="option">你提交了学历认证信息</p>
            <div class="imgs">
              <img v-if="educationPicList && educationPicList.length" :src="`/api/member/pic?id=${educationPicList[0]}&type=5&targetMember=${userId}`" class="img"/>
              <img src="~@/assets/img/certed.png" alt="已认证" class="certed" v-if="status === 3"/>
            </div>
          </li>
          <li class="process-item">
            <p class="option" v-if="status === 1">待后台审核…</p>
            <template v-if="status === 2">
              <p class="date">{{userData.academicAuthTime | formatTime}}</p>
              <p class="option">后台审核不通过，</p>
              <p class="reason"><span class="strong">不通过原因：</span>{{userData.academicAuthReason}}</p>
            </template>
            <template v-if="status === 3">
              <p class="date">{{userData.academicAuthTime | formatTime}}</p>
              <p class="option">后台审核通过</p>
            </template>
          </li>
        </ul>
        <a href="javascript:;" class="re-commit common-btn" v-if="status === 2" @click="reCert">重新认证</a>
      </div>
    </template>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from 'vuex'
import { isIOS, transferBytes } from '@/utils/tools'

const getRotate = () => {
  return import('@/utils/rotateImg.js')
}

export default {
  name: 'uploadCert',
  computed: {
    ...mapState({
      userData: 'userData'
    }),
    ...mapGetters({
      userId: 'userId'
    }),
    status () {
      return this.userData.academicAuth
    }
  },
  data () {
    return {
      // 0: 未上传; 1: 待审核; 2: 未通过; 3: 通过
      educationPicList: null
    }
  },
  watch: {
    userData (val) {
      if (val) {
        this.educationPicList = this.userData.educationPicList
      }
    }
  },
  methods: {
    ...mapMutations({
      updateUserData: 'updateUserData'
    }),
    handleImg (e) {
      let target = e.target
      let file = target.files[0]
      if (isIOS()) {
        getRotate().then(({ rotatePhoto }) => {
          rotatePhoto(file, (res) => {
            this.uploadImg(res, target)
          })
        })
      } else {
        this.uploadImg(file, target)
      }
    },
    uploadImg (file, target) {
      this.$root.openLoading()
      // 将文件转换成bytes后上传
      transferBytes(file).then(res => {
        this.$http('/api/member/upload/pic', {
          currentMemberId: this.userId,
          type: 5,
          pic: res
        }).then(res => {
          this.$root.closeLoading()
          if (res.code === '0000') {
            this.educationPicList = [res.data]
          } else {
            this.$toast(res.msg)
            target.value = ''
          }
        }, err => {
          this.$root.closeLoading()
          this.$toast(err)
          target.value = ''
        })
      }, err => {
        this.$root.closeLoading()
        this.$toast(err)
        target.value = ''
      })
    },
    commit () {
      if (this.educationPicList.length) {
        this.$root.openLoading()
        // 保存个人信息
        this.$http('/api/member/save', {
          currentMemberId: this.userId,
          educationPicList: this.educationPicList,
          academicAuth: 1
        }).then(res => {
          this.$root.closeLoading()
          if (res.code === '0000') {
            // 更新个人信息
            this.updateUserData({
              educationPicList: this.educationPicList,
              academicAuth: 1,
              academicCommitTime: new Date().getTime()
            })
          } else {
            this.$toast(res.msg)
          }
        }, err => {
          this.$root.closeLoading()
          this.$toast(err)
        })
      }
    },
    reCert () {
      this.updateUserData({
        educationPicList: null,
        academicAuth: 0
      })
    }
  },
  mounted () {
    if (this.userData.educationPicList) {
      this.educationPicList = this.userData.educationPicList
    }
  }
}
</script>

<style lang="scss">
@import "@/assets/scss/constant.scss";
.upload-cert-container.upload-education-container {
  .imgs {
    .img {
      margin-right: 20px;
      width: 230px;
      height: 155px;
    }
  }
}
</style>
