<template>
  <div class="message-detail-container">
    <div class="head-box">
      <div class="img-box">
        <img :src="`/api/member/pic?id=${fromHeadPicId?fromHeadPicId:0}&type=1&targetMemberId=${fromMemberId}`" alt="" class="img" />
      </div>
    </div>

    <div class="right-con">
      <p class="name-box"><span class="name">{{fromName}}</span><span class="official">官方</span></p>
      <ul class="detail-list" v-for="(item, index) in list" :key="index">
        <li class="detail-item">
          <div class="message">
            {{item.message}}
            <p class="time">{{item.addTime | formatTime}}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import Storage from '@/utils/Storage'
export default {
  name: 'MessageDetail',
  data () {
    return {
      list: [],
      currentId: undefined,
      isShowDialog: false,
      dialogConfirmText: '我知道了',
      notShowAgain: false,
      unreadCount: 0,
      fromMemberId: this.$route.params.id,
      fromName: '',
      fromHeadPicId: 0
    }
  },
  computed: {
    ...mapState({
      userData: 'userData'
    }),
    ...mapGetters({
      userId: 'userId'
    })
  },
  methods: {
    jump (id) {
      this.$router.push({
        path: `/message-detail-${id}.html`
      })
    },
    toDetail () {
      this.$router.push({
        path: `/info-${this.currentId}.html`
      })
    },
    popup (id) {
      this.currentId = id
      if (!Storage.load('neverPopMessage')) {
        this.isShowDialog = true
        return
      }

      // 直接跳转到个人详情页
      this.toDetail()
    },
    checkKnown () {
      this.notShowAgain = !this.notShowAgain
    },
    confirm () {
      if (this.notShowAgain) {
        Storage.save('neverPopMessage', true)
      } else {
        Storage.save('neverPopMessage', false)
      }
    },
    closed () {
      // 直接跳转到个人详情页
      this.toDetail()
    }
  },
  mounted () {
    this.$root.openLoading()
    this.$http('/api/member/message/list', {
      currentMemberId: this.userId,
      fromMemberId: this.fromMemberId
    }, 'get').then(res => {
      this.$root.closeLoading()
      if (res.code === '0000') {
        this.list = res.data.data
        if (this.list.length > 0) {
          this.fromName = this.list[0].name
          this.fromHeadPicId = this.list[0].coverPicList[0]
        }
      } else {
        this.$toast(res.msg)
      }
    }, err => {
      this.$root.closeLoading()
      this.$toast(err)
    })
  }
}
</script>

