<template>
  <div>
    <van-list
      v-model="isLoading"
      :finished="isFinished"
      finished-text="没有更多了"
      @load="fetchData"
      class="users-list"
    >
      <dt class="users-title">
        <span class="title">索要微信</span>
      </dt>
      <dd class="users-item" v-for="(item, index) in list" :key="index" @click="jump(item.id)">
        <div class="head-box">
          <div class="img-box">
            <img :src="`/api/member/pic?id=${item.headPicId}&type=8&targetMemberId=${item.id}`" alt="" class="img" />
          </div>
        </div>
        <div class="infomation">
          <p class="name-box"><span class="name">{{item.nickName}}</span></p>
          <p class="sentence">{{`${parseInt(item.nativeCityId) === 900000 ? (item.nativeCountry + ' ' + item.nativeState + '人') : areaData['city_list'][item.nativeCityId]}人`}} 居住{{`${parseInt(item.cityId) === 900000 ? (item.country + ' ' + item.state) : areaData['city_list'][item.cityId]}`}} {{item.age}}岁 {{item.high}}cm {{educationMap[item.education]}} {{item.ifStudent ? '' : item.job + (item.companyVisible ? ' ' + item.company : '')}}</p>
        </div>
      </dd>
    </van-list>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
export default {
  name: 'Ask',
  data () {
    return {
      isLoading: false,
      isFinished: false,
      currentPage: 1,
      pageSize: 20,
      list: []
    }
  },
  computed: {
    ...mapState({
      educationMap: 'educationMap',
      areaData: 'areaData'
    }),
    ...mapGetters({
      userId: 'userId'
    })
  },
  methods: {
    fetchData () {
      this.isLoading = true
      this.$http('/api/member/action/list', {
        currentPage: this.currentPage,
        pageSize: this.pageSize,
        currentMemberId: this.userId,
        actionType: 30
      }, 'get').then(res => {
        this.isLoading = false
        if (res.code === '0000' && res.data) {
          const data = res.data
          if (this.currentPage === 1) {
            this.list = data.data
          } else {
            this.list = [...this.list, ...data.data]
          }
          try {
            if (this.pageSize > data.data.length || !this.userId) {
              this.isFinished = true
            }
          } catch (err) {
            this.isFinished = true
          }
          this.currentPage++
        } else {
          this.isFinished = true
          this.$toast(res.msg)
        }
      }, err => {
        this.isFinished = true
        this.isLoading = false
        this.$toast(err)
      })
    },
    jump (userId) {
      this.$router.push({
        path: `/info-${userId}.html`
      })
    }
  }
}
</script>
