<template>
  <div class="detail-info-container">
    <div class="img-swiper" v-if="targetData.coverPicList">
      <Swipe :autoplay="0" indicator-color="white" class="img-box">
        <SwipeItem class="img-box" v-for="img in targetData.coverPicList" :key="img">
          <img class="img" :src="`/api/member/pic?id=${img}&type=1&targetMemberId=${targetData.id}`" />
        </SwipeItem>
      </Swipe>
      <span class="img-num">{{targetData.coverPicList.length}}</span>
    </div>
    <div class="info-box" v-if="targetData.id">
      <div class="name-op-con">
        <span class="name-con">{{targetData.nickName}}<img class="identify-icon" src="~@/assets/img/identify.png"/></span>
        <span class="op-con">
          <a href="javascript:;" class="report btn" @click="switchReport">举报</a>
          <a href="javascript:;" class="follow btn" :class="{'followed': targetData.follow}" @click="switchFollow">{{targetData.follow ? '取关' : '关注'}}</a>
        </span>
      </div>
      <div class="id-con" ref="code-con"><strong class="id">ID: {{targetData.id}}</strong><a href="javascript:;" class="copy-btn" @click="execCopy(targetData.id)">复制</a></div>
      <p class="info-con">{{targetData.age}}岁 {{targetData.high}}cm {{constellationMap[targetData.constellation]}} {{educationMap[targetData.education]}}{{targetData.ifStudent ? '在读' : ''}}{{targetData.school ? `(${targetData.school})` : ''}}</p>
      <p class="info-con desc">{{targetData.industry ? `${targetData.industry}行业 ` : ''}}{{targetData.job}}{{targetData.companyVisible && targetData.company ? `（${targetData.company}）` : ''}}</p>
      <p class="info-con">{{statusMap[targetData.singleStatus]}} {{assetsMap[targetData.richesInfo]}} {{targetData.annualSalaryVisible ? salaryMap[targetData.annualSalary] : ''}}</p>
      <p class="info-con location">现居{{`${parseInt(targetData.cityId) === 900000 ? (targetData.country + ' ' + targetData.state) : areaData['city_list'][targetData.cityId]}`}} · {{`${parseInt(targetData.nativeCityId) === 900000 ? (targetData.nativeCountry + ' ' + targetData.nativeState) : areaData['city_list'][targetData.nativeCityId]}`}}人</p>
    </div>

    <div class="desc-list" v-if="targetData.id">
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/self-description.png" alt="" class="self-description" />自我描述</p>
        <p class="desc-detail">{{targetData.selfDescription}}</p>
      </div>
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/family.png" alt="" class="self-description" />家庭背景</p>
        <p class="desc-detail">{{targetData.familyInfo}}</p>
      </div>
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/experice.png" alt="" class="self-description" />个人经历</p>
        <p class="desc-detail">{{targetData.personalExperience}}</p>
      </div>
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/hobby.png" alt="" class="self-description" />兴趣爱好</p>
        <p class="desc-detail">{{targetData.hobby}}</p>
      </div>
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/economic.png" alt="" class="self-description" />经济能力</p>
        <p class="desc-detail">{{targetData.richesInfo}}</p>
      </div>
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/love.png" alt="" class="self-description" />爱情观</p>
        <p class="desc-detail">{{targetData.loveMind}}</p>
      </div>
      <div class="desc-item">
        <p class="desc-top"><img src="~@/assets/img/require.png" alt="" class="self-description" />理想的另一半</p>
        <p class="desc-detail">{{targetData.ideaPartner}}</p>
      </div>
    </div>

    <div class="btns">
      <div class="like-btn btn" :class="{'liked': targetData.like}" @click="clickLike">
        <p class="like word">
          <img v-if="targetData.like" src="~@/assets/img/liked.png" alt="" class="like-icon" />
          <img v-else src="~@/assets/img/like.png" alt="" class="like-icon" />
          {{targetData.like ? '已喜欢TA' : '喜欢ta'}}
        </p>
        <p class="remain-quanitity" v-if="userId">剩余{{userData.likeBalance || 0}}个喜欢名额</p>
      </div>
      <div class="apply-btn btn" :class="{'got': targetData.wechatPicId}" @click="getWebChatCode">
        <p class="word"><img src="~@/assets/img/webchat-logo.png" alt="" class="apply-icon" />{{targetData.wechatPicId ? 'TA的微信' : '索要微信'}}</p>
      </div>
    </div>

    <van-dialog v-model="confirmPurchaseDialog" confirmButtonText="继续支付" :show-cancel-button="true" @confirm="purchase">
      <div class="confirm-purchase-content">
        <h3 class="price-con">
          <p class="cur-price">￥{{transferPrice(purchaseItem.currentPrice)}}<span class="unit">/次</span></p>
          <p class="origin-price">￥{{transferPrice(purchaseItem.originPrice)}}<span class="unit">/次</span></p>
        </h3>
        <p class="tips">支付后会看到对方微信二维码，<strong class="strong">添加对方微信时请备注：ID-学历-年龄-身高。</strong>对方是否通过取决于对方，与平台无关，且购买费用无法退款，请知悉！</p>
      </div>
    </van-dialog>

    <van-dialog v-model="isShowCodeDialog" confirmButtonText="知道了" :message="overlimit ? '你的当日索要次数已达上限（每日30次）请明日再来索要' : ''">
      <div class="target-code-dialog" v-if="!overlimit">
        <p class="target-name">{{targetData.name}}</p>
        <img :src="`/api/member/wechat/pic?id=${targetData.wechatPicId}&type=4&targetMemberId=${targetData.id}`" alt="" class="code" />
        <p class="tips">截图保存后微信扫描即可添加对方<br/>您的ID信息已复制，添加对方微信时，<br/>请粘贴至身份验证框</p>
      </div>
    </van-dialog>

    <van-dialog v-model="showErrorDialog"
      :message="errorDialogContent.message"
      :confirmButtonText="errorDialogContent.confirmText"
      :show-cancel-button="true"
      @confirm="handleErrorDialogClose"
    ></van-dialog>

    <!-- 举报弹窗 -->
    <van-popup v-model="isReporting" :close-on-popstate="true" position="right">
      <div class="report-content">
        <div class="title-con">
          <a @click="switchReport" href="javascript:;" class="btn"><img src="~@/assets/img/back-icon.png" class="back-icon" /></a>
          <h3 class="title">举报</h3>
        </div>
        <div class="input-cotent">
          <textarea ref="reportInputContent" class="textarea" placeholder="请填写10个字以上的举报问题描述，以便我们核实该用户的不良行为" v-model="reportData.description"></textarea>
        </div>
        <div class="upload-box">
          <p class="upload-title">请上传1-3张证据截图</p>
          <div class="upload-content">
            <div class="upload-item" v-for="(img, index) in reportData.imgData" :key="index">
              <img :src="img.url" alt="" class="img-item" />
              <img src="~@/assets/img/close-white.png" alt="" class="delete-btn" @click.stop="deleteImg(index)"/>
            </div>
            <div class="upload-item select-item" v-if="reportData.imgData.length < 3" >
              <input type="file" class="upload-input" accept="image/*" ref="file" @change="selectImg"/>
              <img src="~@/assets/img/upload-bg.png" alt="" class="upload-btn" />
            </div>
          </div>
        </div>
        <a href="javascript:;" class="commit-report" :class="{disable: disableReport}" @click="submitReport">确认举报</a>
      </div>
    </van-popup>

    <!-- 加速审核弹窗 -->
    <AccelerateAduit v-model="isShowAccelerate" :isBaseData="isBaseData" @confirm="isShowAccelerate = false" @close="isShowAccelerate = false"/>

    <!-- 购买喜欢数量弹窗 -->
    <purchaseModal :show="isShowModal" @close="isShowModal=false" @callback="isShowModal=false"/>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from 'vuex'
import { copy, isIOS, transferBytes } from '@/utils/tools'
import wxApi from '@/utils/wxAuth'
import { Toast, Swipe, SwipeItem } from 'vant'
import purchaseModal from '@/components/common/purchaseModal'
import AccelerateAduit from '@/components/dialog/accelerateAduit'

const getRotate = () => {
  return import('@/utils/rotateImg.js')
}
export default {
  name: 'detailInfo',
  components: {
    AccelerateAduit,
    purchaseModal,
    Swipe,
    SwipeItem
  },
  data () {
    return {
      targetData: {
      },
      isReporting: false,
      reportData: {
        description: '',
        imgData: []
      },
      preImgArr: [],
      overlimit: false,
      purchaseItem: {},
      confirmPurchaseDialog: false,
      // 微信二维码弹窗
      isShowCodeDialog: false,
      // 错误状态提示弹窗
      errorStatus: 0,
      showErrorDialog: false,
      errorDialogContent: {
        message: '',
        confirmText: ''
      },
      // 购买 喜欢数量 弹窗
      isShowModal: false,
      // 是否显示加速审核弹窗
      isShowAccelerate: false,
      isBaseData: true
    }
  },
  computed: {
    ...mapState({
      userData: 'userData',
      educationMap: 'educationMap',
      salaryMap: 'salaryMap',
      assetsMap: 'assetsMap',
      statusMap: 'statusMap',
      constellationMap: 'constellationMap',
      areaData: 'areaData'
    }),
    ...mapGetters({
      userId: 'userId'
    }),
    id () {
      return this.$route.params.id
    },
    disableReport () {
      return this.reportData.description.length < 10 || !this.reportData.imgData.length
    }
  },
  watch: {
    isReporting (val) {
      if (val) {
        this.$nextTick(() => {
          let $ref = this.$refs['reportInputContent']
          $ref && $ref.focus()
        })
      }
    },
    isShowCodeDialog (val) {
      if (val) {
        copy(`${this.userId} - ${this.userData.nickName} - ${this.userData.high} - ${this.userData.age}`).catch(() => {
          this.$toast('复制个人信息失败')
        })
      }
    }
  },
  methods: {
    ...mapMutations({
      updateUserData: 'updateUserData'
    }),
    fetchInfo () {
      if (!this.id) {
        return
      }
      this.$root.openLoading()
      this.$http('/api/member/info', {
        currentMemberId: this.userId,
        targetMemberId: this.id
      }, 'get').then(res => {
        this.$root.closeLoading()
        if (res.code === '0000') {
          const d = res.data
          this.targetData = d
          wxApi.configShare({
            title: 'Hello单身部落',
            // desc: 'ID号-学历-工作-年龄-城市',
            desc: `${d.id}-${this.educationMap[d.education]}-${d.ifStudent ? '在读' : d.job}-${d.age}-${parseInt(d.cityId) === 900000 ? (d.country + ' ' + d.state) : this.areaData['city_list'][d.cityId]}`,
            link: location.href,
            imgUrl: `${location.origin}/api/member/pic?id=${d.headPicId}&type=8&targetMemberId=${d.id}`
            // imgUrl: `${location.origin}/api/member/pic?id=${d.coverPicList[0]}&type=1&targetMemberId=${d.id}`
          })
        } else {
          this.$toast(res.data.msg)
        }
      }, err => {
        this.$root.closeLoading()
        this.$toast(err)
      })
    },
    transferPrice (val) {
      if (typeof val !== 'number') {
        return 0
      } else {
        return (val / 100).toFixed(1)
      }
    },
    /**
     * @description 是否登录状态
     * @returns { Boolean } 通过为true，否则false
     */
    checkLogin () {
      if (!this.userId) {
        // 未登录情况下
        this.errorStatus = 0
        this.errorDialogContent.message = '您当前未登录，请先登录'
        this.errorDialogContent.confirmText = '立即登录'
        this.showErrorDialog = true
        return false
      } else {
        return true
      }
    },
    /**
     * @description 基础资料审核状态判断
     * @returns { Boolean } 通过为true，否则false
     */
    checkBaseData () {
      if (!this.checkLogin()) return
      if (parseInt(this.userData.basedataAuth) !== 3) {
        if (parseInt(this.userData.basedataAuth) === 1) {
          this.isBaseData = true
          this.isShowAccelerate = true
        } else if (parseInt(this.userData.basedataAuth) === 0) {
          this.errorStatus = 1
          this.errorDialogContent.message = '您当前未通过个人资料审核，请先通过个人资料审核'
          this.errorDialogContent.confirmText = '完善资料'
          this.showErrorDialog = true
        } if (parseInt(this.userData.basedataAuth) === 2) {
          this.errorStatus = 1
          this.errorDialogContent.message = '资料未通过审核，请修改重新提交'
          this.errorDialogContent.confirmText = '修改资料'
          this.showErrorDialog = true
        }
        return
      }
      return true
    },
    /**
     * @description 权限检查
     * @returns { Boolean } 通过为true，否则false
     */
    checkStatus () {
      if (!this.checkBaseData()) return
      // 权限判断
      if (parseInt(this.userData.identityAuth) === 2 ||
        parseInt(this.userData.academicAuth) === 2 ||
        parseInt(this.userData.workAuth) === 2) {
        this.isBaseData = false
        this.isShowAccelerate = true
        return
      } else if (
        parseInt(this.userData.identityAuth) !== 3 ||
        parseInt(this.userData.academicAuth) !== 3 ||
        parseInt(this.userData.workAuth) !== 3
      ) {
        this.errorStatus = 2
        this.errorDialogContent.message = '个人证件通过审核后才能索要微信，立即提交个人证件'
        this.errorDialogContent.confirmText = '提交证件'
        this.showErrorDialog = true
        return
      }
      return true
    },
    switchReport () {
      this.isReporting = !this.isReporting
    },
    switchFollow () {
      if (!this.checkLogin()) return
      this.$root.openLoading()
      this.$http(`/api/member/action`, {
        currentMemberId: this.userId,
        targetMemberId: this.id,
        fromMemberId: this.userId,
        toMemberId: this.id,
        type: this.targetData.follow ? 11 : 10
      }).then(res => {
        this.$root.closeLoading()
        if (res.code === '0000') {
          this.targetData.follow = !this.targetData.follow
          let followCount = parseInt(this.userData.followCount)
          if (this.targetData.follow) {
            this.$toast('已成功关注该用户，可在“我的”页面中查看所有关注的人')
            followCount = followCount >= 0 ? followCount + 1 : 1
            this.updateUserData({
              followCount
            })
          } else {
            this.$toast('取消关注成功')
            followCount = followCount > 1 ? followCount - 1 : 0
            this.updateUserData({
              followCount
            })
          }
        } else {
          this.$toast(res.msg)
        }
      }, err => {
        this.$root.closeLoading()
        this.$toast(err)
      })
    },
    clickLike () {
      if (this.targetData.like) {
        return
      }
      if (!this.checkBaseData()) return
      this.$root.openLoading()
      this.$http('/api/member/action', {
        currentMemberId: this.userId,
        targetMemberId: this.id,
        fromMemberId: this.userId,
        toMemberId: this.id,
        type: 20
      }).then(res => {
        this.$root.closeLoading()
        if (res.code === '0000') {
          this.targetData.follow = true
          // 更新喜欢的人数量
          let likeCount = parseInt(this.userData.likeCount)
          likeCount = likeCount >= 0 ? likeCount + 1 : 1
          this.updateUserData({
            likeCount
          })
          // 更新 “喜欢” 剩余个数
          let likeBalance = parseInt(this.userData.likeBalance)
          likeBalance = likeBalance >= 0 ? likeBalance - 1 : 1
          this.updateUserData({
            likeBalance
          })
          if (res.data['wechatPicId']) {
            this.targetData.wechatPicId = res.data.wechatPicId
            this.isShowCodeDialog = true
          } else {
            this.targetData.like = true
            this.$toast('ta已收到你的喜欢，若对方回点你喜欢，则自动公开彼此微信号')
          }
        } else if (res.code === '200001') {
          // 喜欢数量不足
          this.isShowModal = true
        } else {
          this.$toast('喜欢失败!')
        }
      }, err => {
        this.$root.closeLoading()
        this.$toast(err)
      })
    },
    /**
     * @description 购买微信前置判断
     */
    async getWebChatCode () {
      if (this.targetData.wechatPicId) {
        // 已经获取到微信，直接弹出微信二维码弹窗
        this.isShowCodeDialog = true
      } else if (!this.checkStatus()) {} else if (this.userData.overlimit) {
        this.overlimit = true
      } else {
        let { code, data, msg } = await this.$http('/api/member/product/list', { type: 2 }, 'get')
        if (!(code === '0000' && data)) return this.$toast(msg)
        // 打开确认购买弹窗
        this.purchaseItem = data[0]
        this.confirmPurchaseDialog = true
      }
    },
    /**
     * @description 购买微信
     */
    async purchase () {
      try {
        var fd = new FormData()
        fd.append('targetMemberId', this.id)
        fd.append('productId', this.purchaseItem.id)
        let { code, data, msg } = await this.$http('/api/member/purchase/transaction', fd)
        if (!(code === '0000' && data)) {
          return Toast(msg)
        }
        // 微信支付
        this.$http('/api/login/wx/jssdk/config/get', {
          currentMemberId: this.userId || 0,
          pageUrl: location.href
        }, 'get').then(res => {
          if (res.code === '0000') {
            wxApi.init({
              appid: data.appId, // 正式测试号
              redirectUri: location.href
            }).wexinPay(data, res.data).then(res => {
              if (res.actionCode === 200) {
                this.$toast('购买成功')
                this.targetData = Object.assign(this.targetData, res.data)
                this.isShowCodeDialog = true
              } else {
                this.$toast('您取消了支付')
              }
            }).catch(e => {
              Toast('支付异常')
            })
          }
        }, err => {
          this.$root.closeLoading()
          this.$toast(err)
        })
      } catch (error) {
        Toast(error)
      }
    },
    handleErrorDialogClose () {
      switch (this.errorStatus) {
        case 0:
          this.$router.push({
            path: '/account.html',
            query: {
              redirect: this.$route.path
            }
          })
          break
        case 1:
          // 基础资料不通过。待审核状态，跳到个人主页，否则直接跳转到个人资料页
          if (parseInt(this.userData.basedataAuth) === 1) {
            this.$router.push({
              path: '/account.html'
            })
          } else {
            this.$router.push({
              path: '/baseInfo.html'
            })
          }
          break
        case 2:
          this.$router.push({
            path: '/account.html'
          })
          break
      }
    },
    execCopy (text) {
      copy(text, this.$refs['code-con']).then(res => {
        this.$toast('复制成功')
      }, () => {
        this.$toast('复制失败')
      })
    },
    getPics () {
      let pics = []
      for (let i = 0; i < this.reportData.imgData.length; i++) {
        pics.push(this.reportData.imgData[i].id)
      }
      return pics
    },
    submitReport () {
      if (this.disableReport) return
      let fd = {}
      fd.currentMemberId = this.userId
      fd.targetMemberId = this.id
      fd.message = this.reportData.description
      fd.pics = this.getPics()
      this.$root.openLoading()
      this.$http('/api/member/report', fd).then(res => {
        this.$root.closeLoading()
        if (res.code === '0000') {
          this.$toast('已成功举报该用户，后续处理结果将通过电话告知给您，敬请关注')
          setTimeout(() => {
            this.isReporting = false
          }, 1000)
        } else {
          this.$toast(res.msg)
        }
      }, err => {
        this.$root.closeLoading()
        this.$toast(err)
      })
    },
    postImg (base64) {
      let params = {}
      params.currentMemberId = this.userData.id
      params.type = '3'
      params.pic = base64
      this.$root.openLoading()
      this.$http('/api/member/upload/pic', params).then(res => {
        this.$root.closeLoading()
        if (res.code === '0000') {
          this.picIdList.push(res.data)
        } else {
          this.$toast(res.msg)
        }
      }, err => {
        this.$root.closeLoading()
        this.$toast(err)
      })
    },
    getImg (e) {
      let files = document.getElementById('img').files[0]
      // 转码base64
      let reader = new FileReader()
      reader.readAsDataURL(files)
      reader.onload = e => {
        this.imgBase64 = e.target.result
      }
    },
    selectImg (e) {
      let file = e.target.files[0]
      if (isIOS()) {
        getRotate().then(({ rotatePhoto }) => {
          rotatePhoto(file, (res) => {
            this.setImg(res)
          })
        })
      } else {
        this.setImg(file)
      }
    },
    setImg (img) {
      transferBytes(img).then(res => {
        this.$http('/api/member/upload/pic', {
          currentMemberId: this.userId,
          type: 3,
          pic: res
        }).then(resUpload => {
          this.$root.closeLoading()
          if (resUpload.code === '0000') {
            let url = URL.createObjectURL(img)
            let imgReport = {}
            imgReport.id = resUpload.data
            imgReport.url = url
            this.reportData.imgData.push(imgReport)
            this.$refs['file'].value = null
          } else {
            this.$toast(resUpload.msg)
          }
        }, err => {
          this.$root.closeLoading()
          this.$toast(err)
        })
      })

      this.$refs['file'].value = null
    },
    deleteImg (index) {
      this.reportData.imgData.splice(index, 1)
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.fetchInfo()
    })
  },
  beforeRouteLeave (to, from, next) {
    this.targetData = {}
    wxApi.configShare()
    next()
  }
}
</script>

<style lang="scss">
@import "@/assets/scss/constant.scss";
.detail-info-container {
  padding: 30px 40px 180px;
  .img-swiper {
    position: relative;
    height: 470px;
    border-radius: 8px;
    overflow: hidden;
  }
  .img-box {
    height: 100%;
