export function setCookie (key, val, days, path, domain = '.hellobuluo.com') {
  let expire = new Date()
  expire.setTime(
    expire.getTime() +
      (days ? 3600000 * 24 * days : 30 * 24 * 60 * 60 * 1000)
  ) // 默认1个月
  document.cookie =
    key +
    '=' +
    encodeURIComponent(val) +
    ';expires=' +
    expire.toGMTString() +
    ';' +
    (domain ? 'domain=' + domain + ';' : '')
}

export function getCookie (key) {
  let r = new RegExp('(?:^|;+|\\s+)' + key + '=([^;]*)')
  let m = window.document.cookie.match(r)
  let rs = !m ? '' : m[1]
  return decodeURIComponent(rs)
}

export function delCookie (key, path, domain = '.hellobuluo.com') {
  document.cookie =
    key +
    '=;expires=Thu, 01 Jan 1970 00:00:00 GMT;' +
    (domain ? 'domain=' + domain + ';' : '')
}

export function insertScript (src, onload, async, id) {
  let head = document.getElementsByTagName('head')[0]
  let script = document.createElement('script')
  script.type = 'text/javascript'
  script.src = src
  script.onload = function () { onload && onload(script) }
  script.async = async || false
  if (id) {
    script.setAttribute('id', id)
  }
  head.appendChild(script)
}

export function isIOS () {
  return /iphone|ipad/i.test(navigator.userAgent)
}

export function copy (text, dom = document.body) {
  return new Promise((resolve, reject) => {
    if (!document.queryCommandSupported('copy')) {
      reject(new Error())
      return
    }
    let container = dom || document.body
    const input = document.createElement('input')
    input.setAttribute('readonly', 'readonly')
    input.setAttribute('value', text)
    container.appendChild(input)
    input.select()
    input.setSelectionRange(0, ('' + text).toString().length)
    try {
      var successful = document.execCommand('copy')
      dom.removeChild(input)
      if (successful) {
        resolve()
      } else {
        reject(new Error())
      }
    } catch (err) {
      dom.removeChild(input)
      reject(err)
    }
  })
}

export function transferBytes (file) {
  return new Promise((resolve, reject) => {
    try {
      let reader = new FileReader()
      let fileByteArray = []
      reader.readAsArrayBuffer(file)
      reader.onloadend = function (evt) {
        if (evt.target.readyState === FileReader.DONE) {
          let arrayBuffer = evt.target.result
          let array = new Uint8Array(arrayBuffer)
          for (var i = 0; i < array.length; i++) {
            fileByteArray.push(array[i])
          }
          resolve(fileByteArray)
        }
      }
    } catch (err) {
      reject(err)
    }
  })
}
