<template>
  <div class="account-container">
    <template v-if="userId">
      <MyHeader>
        <div class="btn-con" slot="nav-left">
          <router-link to="/setting.html" class="header-btn"><img src="~@/assets/img/setting.png"
              class="setting-icon" /></router-link>
        </div>
        <div class="btn-con" slot="nav-right">
          <router-link to="/customer-service.html" class="header-btn"><img src="~@/assets/img/service.png"
              class="service-icon" /></router-link>
        </div>
      </MyHeader>
      <div class="account-content">
        <div class="information-box">
          <div class="my-data">
            <div class="head img-box">
              <img :src="`/api/member/pic?id=${userData.headPicId}&type=8&targetMemberId=${userId}`" alt=""
                class="img" />
            </div>
            <div class="username-box">
              <h3 class="username">{{userData.nickName}}</h3>
              <p class="id-con" ref="code-con">ID {{userId}}<a class="copy-btn" href="javascript:;"
                  @click.stop="execCopy(userId)">复制</a></p>
            </div>
          </div>
          <div class="able-like">
            <p class="like-num">喜欢×{{userData.likeBalance}}</p>
            <a href="javascript:;" class="purchase-btn" @click="purchase">购买</a>
          </div>
        </div>
        <div class="status-box">
          <router-link to="/like.html" class="status-item">
            <span class="status-icon"></span>
            <span class="status-info">我喜欢的 {{userData.likeCount}}</span>
          </router-link>
          <router-link to="/likeme.html" class="status-item">
            <span class="status-icon"></span>
            <span class="status-info">喜欢我的 {{userData.belikedCount}}</span>
          </router-link>
          <router-link to="/follow.html" class="status-item">
            <span class="status-icon"></span>
            <span class="status-info">关注的 {{userData.followCount}}</span>
          </router-link>
          <router-link to="/ask.html" class="status-item">
            <span class="status-icon"></span>
            <span class="status-info">索要微信 {{userData.getwechatCount}}</span>
          </router-link>
        </div>
      </div>
      <dl class="information-content base-information">
        <dt class="information-title">
          <span class="title">基础资料</span>
          <span class="introduce">（完成基础资料后，可在平台展示个人信息）</span>
        </dt>
        <dd class="information-item" @click="toBaseInfo">
          <p class="item-type">
            <span class="icon"></span>
            <span class="type-name">完善基础资料</span>
          </p>
          <p class="item-status" :class="transferStatus(userData.basedataAuth).class">
            <span class="status-name">{{transferStatus(userData.basedataAuth).text}}</span>
            <img src="~@/assets/img/arrow-enter.png" alt="" class="icon" />
          </p>
        </dd>
      </dl>
      <dl class="information-content certificate-information">
        <dt class="information-title">
          <span class="title">证件审核</span>
          <span class="introduce">（以下认证全部完成奖励3个喜欢的名额）</span>
        </dt>
        <dd class="information-item" @click="jumpTo('id')">
          <p class="item-type cert">
            <span class="icon"></span>
            <span class="type-name">身份认证</span>
          </p>
          <p class="item-status" :class="transferStatus(userData.identityAuth).class">
            <span class="status-name">{{transferStatus(userData.identityAuth).text}}</span>
            <img src="~@/assets/img/arrow-enter.png" alt="" class="icon" />
          </p>
        </dd>
        <dd class="information-item" @click="jumpTo('education')">
          <p class="item-type education">
            <span class="icon"></span>
            <span class="type-name">学历认证</span>
          </p>
          <p class="item-status" :class="transferStatus(userData.academicAuth).class">
            <span class="status-name">{{transferStatus(userData.academicAuth).text}}</span>
            <img src="~@/assets/img/arrow-enter.png" alt="" class="icon" />
          </p>
        </dd>
        <dd class="information-item" @click="jumpTo('career')">
          <p class="item-type work">
            <span class="icon"></span>
            <span class="type-name">工作认证</span>
          </p>
          <p class="item-status" :class="transferStatus(userData.workAuth).class">
            <span class="status-name">{{transferStatus(userData.workAuth).text}}</span>
            <img src="~@/assets/img/arrow-enter.png" alt="" class="icon" />
          </p>
        </dd>
      </dl>

      <div class="information-content condition">
        <div class="information-title">
          <span class="title">择偶标准</span>
          <span class="introduce">（选择标准后，首页精准展示）</span>
          <span class="edit-btn" @click="switchConditionPop"><img src="~@/assets/img/edit.png"
              class="edit-icon" /></span>
        </div>
        <div class="condition-content">
          <p class="condition-item">
            <span class="condition-type">年龄</span>
            <span
              class="condition-value">{{userData.selectionAgeMin || userData.selectionAgeMax ? `${userData.selectionAgeMin ? userData.selectionAgeMin : '不限'}-${userData.selectionAgeMax ? userData.selectionAgeMax : '不限'}岁` : '不限'}}</span>
          </p>
          <p class="condition-item">
            <span class="condition-type">身高</span>
            <span
              class="condition-value">{{userData.selectionHighMin || userData.selectionHighMax ? `${userData.selectionHighMin ? userData.selectionHighMin : '不限'}-${userData.selectionHighMax ? userData.selectionHighMax : '不限'}cm` : '不限'}}</span>
          </p>
          <p class="condition-item">
            <span class="condition-type">学历</span>
            <span
              class="condition-value">{{userData.selectionAcadmic ? educationMap[userData.selectionAcadmic] : '不限'}}</span>
          </p>
          <p class="condition-item">
            <span class="condition-type">地区</span>
            <span
              class="condition-value">{{userData.selectionCityId ? parseInt(userData.selectionCityId) === 900000 ? `${userData.selectCountry} ${userData.selectState}` : areaData['city_list'][userData.selectionCityId] : '不限'}}</span>
          </p>
          <p class="condition-item">
            <span class="condition-type">感情状态</span>
            <span
              class="condition-value">{{userData.selectionSingleStatus != null ? statusMap[userData.selectionSingleStatus] : '不限'}}</span>
          </p>
        </div>
      </div>

      <div class="rules">
        <h3 class="rule-title">真诚、严肃、平等交友 </h3>
        <p class="rule">1、本平台的信息资料皆来源于用户本身，平台只是做信息的展示和功能的实现，用户需对自己提供的资料真实性负全部责任，如有虚假信息欺诈行为，本平台有权追责。</p>
        <p class="rule">
          2、用户上传证件资料（如身份证、营业执照、工作证、劳动合同等）委托本平台做实名审核，审核通过后则成为平台的实名用户。本平台只做资料核对和形式上的审查，真实度有限，仅可作为实名参考，并不承担任何担保。</p>
        <p class="rule">3、 请大家在使用本平台交友过程中保护好自己， 对自身的人身和财产安全负全部责任， 做好安全防范措施，避免财产往来和过密接触。因此而产生的的纠纷违法等行为，平台本身不负责任何法律责任。</p>
        <p class="rule">4、如遇到虚假信息、欺骗行为、不良目的或人品素质等有问题的用户，欢迎向本平台投诉并举证。平台将视具体情况做出相应的处理（如限制该用户权限、账号封禁、全平台公告等方式），以保障他人的权益和安全。
        </p>
      </div>

      <van-popup v-model="showConditionPop" position="right">
        <div class="edit-condition">
          <MyHeader title="择偶标准" :back="switchConditionPop" />
          <div class="edit-condition-content">
            <p class="sub-title">请告诉我们你心目中的ta</p>

            <div class="sort-item">
              <span class="sort-name">年龄(岁)</span>
              <div class="sort-input">
                <!-- <input v-model="filterData.selectionAgeMin" type="tel" class="input" name="selectionAgeMin"/> -->
                <span class="input" @click.stop="triggerFilterPicker(1)">{{filterData.selectionAgeMin}}</span>
                <span class="separator">至</span>
                <span class="input" @click.stop="triggerFilterPicker(2)">{{filterData.selectionAgeMax}}</span>
                <!-- <input v-model="filterData.selectionAgeMax" type="tel" class="input" name="selectionAgeMax"/> -->
              </div>
            </div>

            <div class="sort-item">
              <span class="sort-name">身高(cm)</span>
              <div class="sort-input">
                <span class="input" @click.stop="triggerFilterPicker(3)">{{filterData.selectionHighMin}}</span>
                <span class="separator">至</span>
                <span class="input" @click.stop="triggerFilterPicker(4)">{{filterData.selectionHighMax}}</span>
              </div>
            </div>

            <div class="sort-item">
              <span class="sort-name">学历</span>
              <div class="sort-input sort-select" @click="showEduPicker = true">
                <span class="value">{{educationMap[filterData.selectionAcadmic]}}</span>
                <a href="javascript:;" class="enter-btn">
                  <img src="~@/assets/img/arrow-enter.png" alt="" class="enter" />
                </a>
              </div>
            </div>

            <div class="sort-item area-item">
              <span class="sort-name">地区</span>
              <div class="area-con">
                <div class="sort-input sort-select" @click.stop="switchAreaPicker">
                  <span class="value">{{areaData['city_list'][filterData.selectionCityId]}}</span>
                  <a href="javascript:;" class="enter-btn">
                    <img src="~@/assets/img/arrow-enter.png" alt="" class="enter" />
                  </a>
                </div>
                <div class="foreign-input sort-select" v-if="isForeign">
                  <input type="text" placeholder="海外国家名" class="input" v-model="filterData.selectCountry" />
                  <input type="text" placeholder="海外地区名" class="input" v-model="filterData.selectState" />
                </div>
              </div>
            </div>

            <div class="sort-item">
              <span class="sort-name">感情状态</span>
              <div class="sort-input sort-select" @click="showStatusPicker = true">
                <span class="value">{{statusMap[filterData.selectionSingleStatus]}}</span>
                <a href="javascript:;" class="enter-btn">
                  <img src="~@/assets/img/arrow-enter.png" alt="" class="enter" />
                </a>
              </div>
            </div>
          </div>
          <div class="btns">
            <a href="javascript:;" class="reset-btn" @click="resetCondition">清空筛选条件</a>
            <a href="javascript:;" class="ensure-btn" @click="uploadCondition">确定</a>
          </div>
        </div>
      </van-popup>

      <van-popup v-model="showEduPicker" position="bottom">
        <van-picker show-toolbar :visible-item-count="3" :columns="EduColumns" @cancel="showEduPicker = false"
          @confirm="onConfirmEdu" />
      </van-popup>

      <van-popup v-model="showStatusPicker" position="bottom">
        <van-picker show-toolbar :visible-item-count="3" :columns="statusColumns" @cancel="showStatusPicker = false"
          @confirm="onConfirmStatus" />
      </van-popup>

      <van-popup v-model="isShowAreaPicker" position="bottom">
        <van-area :area-list="areaData" :columns-num="areaColumns"
          :value="userData.selectionCityId ? userData.selectionCityId.toString() : '100100'" :show-toolbar="true"
          @change="handleAreachange" @confirm="handleAreaconfirm" @cancel="handleAreacancel"></van-area>
      </van-popup>

      <van-popup v-model="showFilterPicker" position="bottom">
        <van-picker show-toolbar :visible-item-count="3" :default-index="filterDefaultIndex" :columns="filterColumns"
          @cancel="showFilterPicker = false" @confirm="onConfirmFilter" />
      </van-popup>

      <purchaseModal :show="isShowModal" @close="isShowModal=false" @callback="isShowModal=false" />

      <AccelerateAduit v-model="isShowAcceDialog" @confirm="isShowAcceDialog = false"
        @close="isShowAcceDialog = false" />

      <van-dialog v-model="showAuthDialog" :message="authDialogMessage" :confirmButtonText="authButtonText"
        @confirm="jump"></van-dialog>
    </template>

    <Login v-else />
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'

import MyHeader from '@/components/common/myHeader'
import Login from '@/components/common/login'
import purchaseModal from '@/components/common/purchaseModal'
import AccelerateAduit from '@/components/dialog/accelerateAduit'
import { copy } from '@/utils/tools'
import { Toast } from 'vant'

export default {
  name: 'account',
  components: {
    MyHeader,
    Login,
    purchaseModal,
    AccelerateAduit
  },
  data () {
    return {
      isShowModal: false,
      isShowAcceDialog: false,
      showConditionPop: false,
      showEduPicker: false,
      // 婚恋状态数据
      showStatusPicker: false,
      // statusColumns: ['未婚单身', '离异有子', '离异无子', '丧偶有子', '丧偶无子', '脱单恋爱中', '脱单已婚'],
      areaColumns: 2,
      isShowAreaPicker: false,
      isForeign: false,
      // 择偶信息
      showFilterPicker: false,
      filterType: 1,
      filterData: {
        selectionAgeMin: '',
        selectionAgeMax: '',
        selectionHighMin: '',
        selectionHighMax: '',
        selectionAcadmic: 0,
        selectionCityId: '',
        selectCountry: '',
        selectState: ''
      },
      showAuthDialog: false,
      authDialogMessage: '',
      authButtonText: '知道了',
      path: ''
    }
  },
  computed: {
    ...mapState({
      userData: 'userData',
      educationMap: 'educationMap',
      statusMap: 'statusMap',
      areaData: 'areaData'
    }),
    ...mapGetters({
      userId: 'userId'
    }),
    isEditableCert () {
      // 只有基础资料审核通过才能上传证件
      return parseInt(this.userData.basedataAuth) === 3
    },
    EduColumns () {
      let arr = []
      for (let i in this.educationMap) {
        arr.push({
          value: i,
          text: this.educationMap[i]
        })
      }
      return arr
    },
    statusColumns () {
      let arr = []
      for (let i in this.statusMap) {
        arr.push({
          value: i,
          text: this.statusMap[i]
        })
      }
      return arr
    },
    filterColumns () {
      let arr = []
      switch (this.filterType) {
        case 1:
        case 2:
          arr = Array.from(Array(66), (v, k) => k + 15)
          arr.unshift('不限')
          break
        case 3:
        case 4:
          arr = Array.from(Array(81), (v, k) => k + 120)
          arr.unshift('不限')
          break
      }
      return arr
    },
    filterDefaultIndex () {
      let index = 0
      switch (this.filterType) {
        case 1:
          index = this.filterColumns.indexOf(this.filterData.selectionAgeMin)
          break
        case 2:
          index = this.filterColumns.indexOf(this.filterData.selectionAgeMax)
          break
        case 3:
          index = this.filterColumns.indexOf(this.filterData.selectionHighMin)
          break
        case 4:
          index = this.filterColumns.indexOf(this.filterData.selectionHighMax)
          break
      }
      index = index >= 0 ? index : 0
      return index
    }
  },
  watch: {
    userData (val) {
      if (this.userId && val.status === 0) {
        return this.$router.push('/setUserData/sex.html')
      }
      if (this.userId) {
        let filterData = this.filterData
        let userData = this.userData
        filterData.selectionAgeMin = userData.selectionAgeMin
        filterData.selectionAgeMax = userData.selectionAgeMax
        filterData.selectionAcadmic = userData.selectionAcadmic
        filterData.selectionCityId = userData.selectionCityId
        filterData.selectionHighMax = userData.selectionHighMax
        filterData.selectionHighMin = userData.selectionHighMin
        filterData.selectionSingleStatus = userData.selectionSingleStatus
        if (userData.selectionCityId === 90000) {
          this.areaColumns = 1
        } else {
          this.areaColumns = 2
        }
      }
    }
  },
  activated () {
    if (this.userId) {
      this.$root.openLoading()
      this.fetchUserData().then(() => {
        this.$root.closeLoading()
      }).catch(() => {
        this.$root.closeLoading()
      })
    }
  },
  methods: {
    ...mapActions({
      fetchUserData: 'fetchUserData'
    }),
    ...mapMutations({
      updateUserData: 'updateUserData'
    }),
    transferStatus (status) {
      let c = {}
      switch (parseInt(status)) {
        case 0:
        case 4:
          c.class = 'static'
          c.text = '待提交'
          break
        case 1:
          c.class = 'emphasize'
          c.text = '待审核'
          break
        case 2:
          c.class = 'emphasize'
          c.text = '审核不通过'
          break
        case 3:
          c.class = 'emphasize'
          c.text = '已审核通过'
          break
      }
      return c
    },
    execCopy (text) {
      copy(text, this.$refs['code-con']).then(res => {
        this.$toast('复制成功')
      }, () => {
        this.$toast('复制失败')
      })
    },
    checkStatus () {
      // 权限判断
      const base = parseInt(this.userData.basedataAuth)
      if (base !== 3) {
        if (base === 0) {
          this.errorStatus = 1
          this.authDialogMessage = '您当前未通过个人资料审核，请先通过个人资料审核'
          this.authButtonText = '完善资料'
          this.path = '/baseInfo.html'
          this.showAuthDialog = true
        } else if (base === 1) {
          this.isShowAcceDialog = true
        } else if (base === 2) {
          this.errorStatus = 1
          this.authDialogMessage = '资料未通过审核，请修改重新提交'
          this.authButtonText = '修改资料'
          this.path = '/baseInfo.html'
          this.showAuthDialog = true
        }
        return
      }
      return true
    },
    purchase () {
      this.isShowModal = true
    },
    toBaseInfo () {
      this.$router.push('/baseInfo.html')
    },
    switchConditionPop (e) {
      if (!this.checkStatus()) return
      e && e.preventDefault()
      this.showConditionPop = !this.showConditionPop
    },
    onConfirmEdu (val) {
      this.filterData.selectionAcadmic = val.value
      this.showEduPicker = false
    },
    onConfirmStatus (val) {
      this.filterData.selectionSingleStatus = val.value
      this.showStatusPicker = false
    },
    triggerFilterPicker (type) {
      this.filterType = type
      this.showFilterPicker = true
    },
    onConfirmFilter (val) {
      this.showFilterPicker = false
      switch (this.filterType) {
        case 1:
          this.filterData.selectionAgeMin = val
          break
        case 2:
          this.filterData.selectionAgeMax = val
          break
        case 3:
          this.filterData.selectionHighMin = val
          break
        case 4:
          this.filterData.selectionHighMax = val
          break
      }
    },
    resetCondition () {
      this.filterData = {
        selectionAgeMin: '',
        selectionAgeMax: '',
        selectionHighMin: '',
        selectionHighMax: '',
        selectionAcadmic: 0,
        selectionCityId: '',
        selectCountry: '',
        selectState: '',
        selectionSingleStatus: null
      }
    },
    uploadCondition () {
      if (this.filterData.selectionAgeMin !== '不限' && this.filterData.selectionAgeMax !== '不限' && this.filterData.selectionAgeMin > this.filterData.selectionAgeMax) {
        this.$toast('年龄范围有误')
        return
      }
      if (this.filterData.selectionHighMin !== '不限' && this.filterData.selectionHighMax !== '不限' && this.filterData.selectionHighMin > this.filterData.selectionHighMax) {
        this.$toast('身高范围有误')
        return
      }
      this.$root.openLoading()
      this.$http('/api/member/save', {
        ...this.filterData,
        currentMemberId: this.userId
      }).then(res => {
        this.$root.closeLoading()
        if (res.code === '0000') {
          this.updateUserData(this.filterData)
        } else {
          Toast(res.msg)
        }
      }).catch(err => {
        this.$toast(err)
        this.$root.closeLoading()
      })
      this.switchConditionPop()
    },
    jumpTo (type) {
      if (this.isEditableCert) {
        switch (type) {
          case 'id':
            this.authDialogMessage = this.userData.identityAuth ? '' : '平台认证你的证件信息仅用于身份认证，不作为其他用途'
            this.path = '/upload-cert.html'
            break
          case 'education':
            this.authDialogMessage = this.userData.academicAuth ? '' : '平台认证你的证件信息仅用于学历认证，不作为其他用途'
            this.path = '/upload-education.html'
            break
          case 'career':
            this.authDialogMessage = this.userData.workAuth ? '' : '平台认证你的证件信息仅用于工作认证，不作为其他用途'
            this.path = '/upload-career.html'
            break
        }
      } else if (!this.checkStatus()) {
        // 基础资料未审核通过前不能上传文件
        return
      }
      if (this.authDialogMessage) {
        this.showAuthDialog = true
      } else {
        this.showAuthDialog = false
        this.jump()
      }
    },
    jump () {
      this.$router.push(this.path)
    },
    switchAreaPicker () {
      this.isShowAreaPicker = !this.isShowAreaPicker
    },
    handleAreacancel () {
      this.isShowAreaPicker = false
    },
    handleAreachange (picker, res) {
      if (res[0].code === '900000') {
        this.areaColumns = 1
      } else {
        this.areaColumns = 2
      }
    },
    handleAreaconfirm (res) {
      this.isShowAreaPicker = false
      // 海外地区特殊处理
      if (res[0].code === '900000') {
        this.isForeign = true
        this.filterData.selectionCityId = res[0].code
      } else {
        this.isForeign = false
        this.filterData.selectionCityId = res[1].code
      }
    }
  }
}
</script>

