  }

  .account-content {
    padding: 35px 40px;
    box-shadow: 0px 1px 2px 0px $mainColor;
    border-radius: 0px 0px 32px 32px;
    .information-box {
      display: flex;
      justify-content: space-between;
    }
    .head-box {
      margin-right: 20px;
      width: 115px;
      height: 115px;
      border-radius: 100%;
      overflow: hidden;
    }
    .username-box {
      display: inline-block;
      vertical-align: middle;
    }
    .username {
      font-size: 42px;
      line-height: 60px;
    }
    .copy-btn {
      margin-left: 20px;
      color: #9b9b9b;
      font-size: 24px;
